import { Component } from '@angular/core';
import { IonicPage, NavController, ModalController, NavParams } from 'ionic-angular';

import { Item } from '../../models/item';
import { Items } from '../../providers/providers';
import { Reviews } from '../../providers/providers';

@IonicPage()
@Component({
  selector: 'card-detail',
  templateUrl: 'card-detail.html'
})
export class CardDetailPage {
  item: any;

  currentReviews: Item[];

  constructor(public navCtrl: NavController, navParams: NavParams, public reviews: Reviews, public items: Items, public modalCtrl: ModalController) {
    this.item = navParams.get('item') || reviews.defaultReview;
    this.currentReviews = this.reviews.query();
  }



  addReview() {
    let addModal = this.modalCtrl.create('ReviewCreatePage');
    addModal.onDidDismiss(item => {
      if (item) {
        this.reviews.add(item);
      }
    })
    addModal.present();
  }

 

}



